:root {
  font-size: 16px;
  font-family: 'Open Sans';
  background: #252525;
  height: 100vh;
}

* {
  box-sizing: border-box;
  margin:0;
  padding:0;
}

main {
  margin-left: 0;
  padding: 0 15px;
}

@media (min-width: 768px) {
  main {
    margin-left:5rem;

  }
}

.home {
  height: 100vh;
  width: 100%;
  display: flex;
}

.home img {
  margin: auto auto;
  display: block;
  transition: 200ms ease;
}


