.sidebar {
  height: 100vh;
  background: #ffb142;
  width:170px;
  transition: 2ms ease-in-out;
  color: white;
  z-index: 4;
  position: absolute;
  top: 0;
  left: 0;
}

.sidebar li {
  list-style: none;
  padding: 40px 0;
}

.sidebar a {
  text-decoration: none;
  color: #282c34;
  font-size: 1.5rem;
}

.sidebar a:hover {
  color: #cc8e35;
}


nav .hamburger {
  width: 80px;
  height: 80px;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  cursor: pointer;
}

nav .stick {
  width: 40px;
  height: 5px;
  border-radius: 4px;
  margin-bottom: 6px;
  background-color: #ffb142;
  display: inline-block;
}

nav .stick:last-child {
  margin-bottom: 0;
}

