.roulette {
  box-sizing: border-box;
  align-self: center;
  width: 100%;
  margin: 0 auto;
  padding: 50px 15px 0;

}

.roulette input {
  font-size: 20px;
  padding: 10px;
  box-sizing: border-box;
  border-radius: 5px;
  width: 50%;
}


.roulette input:before {
  content: 'min/max';
  position: relative;
  display:block;
}

.roulette button {
  padding: 10px 50px;
  background: linear-gradient(0deg,#cc8e35  0%,#ffb142  100% );
  border-radius:10px;
  outline: none;
  border-color: transparent;
  display: block;
  margin: 20px auto;
  width: 100%;
  color: white;
  font-size:30px;
}

.covid {
  display:flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.covid a {
  text-decoration: none;
  color: #cc8e35;
  font-size: 2rem;
  font-weight: bold;
  margin: 60px 0;

}

.covid svg {
  width: 100%;
  height: 100%;
  max-width: 200px;
  max-height: 200px;
  margin-top: 30px;
}

.covid svg .cls-1 {
  fill:#ffb142;
}

.covid svg .cls-2 {
  fill:#cc8e35;
}


@media (min-width: 760px) {
  .twitter {
   width:100%;
  }
  .roulette {
    width:50%;
    padding: 200px 15px 0;
  }
  .sidebar {
    width:300px;
  }

}